						@include font-head(medium);
						font-weight: 700;
						color: $color-base-090;
						width: 100%;
						overflow: hidden;
					}
				}
			}

			.posts-body {
				.added-information {
					margin-bottom: 2.5rem;
				}
			}

			.posts-tag {
				margin-top: 3.25rem;
			}

			.posts-data {
				margin-top: 2rem;
			}
		}

		&.type-a {
			.posts-area {
				position:relative;

				&::before {
					content: '';
					position: absolute;
					width: 100vw;
					height: 1px;
					left: calc(50% - 50vw);
					background: $color-base-020;
				}

				.posts-inform {
					padding-top: 1.5rem;

					.posts-head {
						position: relative;

						.posts-type {
							margin-bottom: 0;
							position: absolute;
							right: 0;
							bottom: 1.75rem;
						}
					}
				}
			}
		}

		&.type-b {
			.posts-area {
				.posts-figure {
					position: relative;
					width: 100vw;
					height: 16.88rem;
					left: calc(50% - 50vw);
					overflow: hidden;

					.posts-thumbnail {
						position: absolute;
						top: 50%;
						left: 50%;
						transform: translate(-50%, -50%);
						min-width: 100%;
						min-height: 100%;

						.image {
							display: block;

						}
					}
				}

				.posts-inform {
					position:relative;
					padding-top: 0.875rem;

					&::before {
						content: '';
						position: absolute;
						top: -10px;
						left: calc(50% - 50vw);
						border-radius: 8px 8px 0 0;
						width: 100vw;
						height: 10px;
						background: #fff;
					}

					.posts-type {
						.data-item {
							.body {
								@include font-body(small);
							}
						}
					}
				}
			}
		}
	}

	&.style-c {
		.posts-inform {
			.posts-account {
				margin: 1.5rem 0;
			}

			.posts-tag {
				margin-top: 3.25rem;
			}

			.posts-data {
				margin-top: 2rem;

				.view + .date {
					margin: 0 0 0 auto;
				}
			}

			.posts-comment {
				position: relative;
				margin-top: 2rem;
				padding-top: 2.5rem;

			}
		}
	}
}

// 글관리 메뉴
.management-menu {
	position: absolute;
	top: 2.5rem;
	right: 1.25rem;
	z-index: 9;

	&:not(.active) {display: none;}

	.head {@include hidden;}

	.body {
		background: #fff;
		border: 1px solid $color-base-030;
	}

	.data-list {
		padding: 0.5rem 0;

		.data-name {
			@include font-body(small);

			display: block;
			padding: 0.5rem 1rem;
			text-decoration: none;
			color: $color-base-090;
		}
	}
}

// 댓글
.comment-display {
	.submit-form {
		.form-area {
			display: block;
			position: relative;
			padding: 0.625rem 0.75rem;
			border: 1px solid $color-base-040;
			min-height: 1.5rem;
			background: #fff;
			border-radius: 0.125rem;

			&:focus-within{
				border-color: $color-base-090;
			}

			&:not(.active) {
				.form-side {
					position: absolute;
					bottom: 0.625rem;
					right: 0.75rem;
				}

				.form {
					&::after {
						padding-bottom: 0;
					}
				}
			}

			.form {
				position: relative;

				.form-elem {
					@include font-body(small);

					position: absolute;
					top: 0;
					left: 0;
					bottom: 0;
					width: 100%;
					padding: 0;
					margin: 0;
					resize: none;
					color: $color-base-090;

					&:focus {
						outline: transparent;
					}

					&::-ms-input-placeholder {color: $color-base-050;}
					&::-moz-placeholder	{color: $color-base-050;}
					&::-moz-placeholder	{color: $color-base-050;}
					&::-webkit-input-placeholder {color: $color-base-050;}
				}

				&::after {
					@include font-body(small);

					content: attr(data-text) ' ';
					display: block;
					min-height: 1.5rem;
					color: transparent;
					white-space: pre-wrap;
					word-wrap: break-word;
					word-break: break-all;
					padding-bottom: 1.25rem;
				}
			}

			.form-side{
				display: flex;
				flex-wrap: wrap;
				justify-content: flex-end;
				z-index: 5;

				.image-area {
					position: relative;
					width: 100%;
					margin-bottom: 0.625rem;

					.image-type {
						position: relative;
						display:block;
						width: 6rem;
						height: 6rem;
						border-radius: 0.25rem;
						background-color: #000;
						background-size: cover;
						background-position: center center;
					}

					.image-clear {
						display: block;
						position: absolute;
						z-index: 1;
						top: 0;
						right: 0;
						width: 1.25rem;
						height: 1.25rem;
						border-top-right-radius: 0.25rem;
						border-bottom-left-radius: 0.25rem;
						background-color: rgba(0,0,0,0.4);
						background-repeat: no-repeat;
						background-position: center center;
						background-size: 1.25rem auto;
						background-image: svgIcon(close, #fff);
					}

					&:not(.typed) {
						display: none;
					}
				}

				.image-uploader {
					position: relative;
					width: 1.5rem;
					height: 1.5rem;
					background-image: svgIcon(camera, $color-base-060);

					&.typed {
						background-image: svgIcon(camera, $color-primary-070);
					}

					.file-form {
						margin: 0;
						padding: 0;
						font-size: 0;
						border: 0 none;
						border-radius: 0;
						background-color: transparent;
						appearance: none;
						position: absolute;
						top: 0;
						left: 0;
						width: 24px;
						height: 24px;

						&::-webkit-file-upload-button,
						&::file-selector-button {
							opacity: 0;
						}

						@at-root {
							html.ie .image-uploader .file-form {
								opacity: 0;
							}
						}
					}

					svg {
						fill: $color-base-060
					}
				}

				.btn.register {
					margin-left: 0.5rem;

					.btn-text {
						padding: 0 0.5rem;
					}
				}
			}

			.target-name {
				position: absolute;
				top: 0.625rem;
				left: 0.75rem;
				margin-left: 0;
			}
		}
	}

	.comment-list {
		.comment-item {
			padding: 1.5rem 0;

			& + .comment-item {
				box-shadow: 0 -0.0625rem 0 0 $color-base-020;
			}

			&.delete {
				padding: 2rem 0;
				text-align: center;

				.comment-delete {
					@include font-body(small);

					color: $color-base-060;
				}
			}

			.comment-inform {
				position: relative;

				.comment-data {
					margin-bottom: 0.75rem;

					.data-list {
						margin: 0;
						padding: 0 2rem 0 0;
						display: flex;
						align-items: center;

						.data-item {
							& + .data-item {
								margin-left: 0.5rem;
							}

							.head {
								@include hidden;
							}

							&.writer {
								.body {
									@include font-body(small);
									
									color: $color-base-090;
									font-weight: 700;
									display: block;
									overflow: hidden;

									.mark {
										margin-right: 0.5rem;
										background-color: transparent;
									}
								}
							}

							&.time {
								.body {
									@include font-body(x-small);
									
									color: $color-base-050;
									display: block;
								}
							}
						}
					}
				}

				.comment-body {
					.comment-text {
						p, a {
							@include font-body(small);

							color: $color-base-070;
						}
						p {margin: 0;}
						a {word-break: break-all;}
					}

					.comment-image {
						margin-top: 0.5rem;

						.image {
							display: block;
							max-width: 9.75rem;
							border-radius: 0.25rem;
							overflow:hidden;
						}
					}

					.button-area {
						margin-top: 1rem;
						display: flex;
						justify-content: space-between;

						.button-item {
							display: flex;

							.btn {
								background-color: transparent;

								& + .btn {
									margin-left:0.5rem;
								}
							}
						}
					}
				}

				.comment-side {
					position: absolute;
					top: 0;
					right: 0;

					.button-management {
						svg {fill: $color-base-050;}
					}

					.management-menu {
						top: 2rem;
						right: 0;

						.data-list {
							padding: 0.5rem 0;

							.data-item {
								word-break: keep-all;
							}
						}
					}
				}

				.submit-form {
					margin-top: 1.5rem;

					.form.theme-a {
						.form-elem {
							padding: 0;
							border: 0;
							background: transparent;

							&:focus {outline: transparent;}
						}
					}

					&:not(.active) {display: none;}
				}
			}
		}
	}

	.reply-list {
		margin: 1.563rem 0 0 0;
		padding: 0;
		list-style: none;

		.reply-item {
			position: relative;
			padding: 1.688rem 1.25rem 1.5rem 2.25rem;
			box-shadow: none;
			background: $color-base-010;
			border-radius: 0.25rem;

			&::before {
				content: '';
				display: block;
				position: absolute;
				top: 1.875rem;
				left: 1.25rem;
				min-width: 1.5rem;
				width: 1.5rem;
				height: 1.5rem;
				background-repeat: no-repeat;
				background-position: 0 0;
				background-size: 100% auto;
				background-image: svgIcon(reply, $color-base-050);
			}

			&.delete {
				padding: 2rem 0;
				text-align: center;

				&::before {
					display: none;
				}

				.reply-delete {
					@include font-body(small);
					
					color: $color-base-070;
				}
			}

			& + .reply-item {
				margin-top: 0.5rem;
			}

			.reply-inform {
				position: relative;

				.reply-data {
					margin-bottom: 0.75rem;

					.data-list {
						margin: 0;
						padding: 0 2rem 0 0;
						display: flex;
						align-items: center;

						.data-item {
							& + .data-item {
								margin-left: 0.5rem;
							}

							.head {
								@include hidden;
							}

							&.writer {
								.body {
									@include font-body(small);
									
									color: $color-base-090;
									font-weight: 700;
									display: block;
									overflow: hidden;

									.mark {
										margin-right: 0.5rem;
										background-color: transparent;
									}
								}
							}

							&.time {
								.body {
									@include font-body(x-small);

									color: $color-base-050;
									display: block;
								}
							}
						}
					}
				}

				.reply-body {
					.reply-text {
						p {
							@include font-body(small);

							color: $color-base-070;
							margin: 0;
						}

						a {
							@include font-body(small);

							color: $color-base-070;
							word-break: break-all;
						}
					}

					.reply-image {
						margin-top: 0.5rem;

						.image {
							display: block;
							max-width: 9.75rem;
							border-radius: 0.25rem;
							overflow:hidden;
						}
					}

					.button-area {
						margin-top: 1rem;
						display: flex;
						justify-content: space-between;

						.button-item {
							display: flex;

							.btn {
								background-color: transparent;

								& + .btn {
									margin-left:0.5rem;
								}
							}
						}
					}
				}

				.reply-side {
					position: absolute;
					top: 0;
					right: 0;

					.button-management {
						svg {
							fill: $color-base-050;
						}
					}

					.management-menu {
						top: 2rem;
						right: 0;

						.data-list {
							padding: 0.5rem 0;

							.data-item {
								word-break: keep-all;
							}
						}
					}
				}

				.submit-form {
					margin-top: 1.5rem;

					.form.theme-a {
						.form-elem {
							padding: 0;
							border: 0;
							background: transparent;

							&:focus {
								outline: transparent;
							}
						}
					}

					&:not(.active) {
						display: none;
					}
				}
			}
		}
	}
}

.posts-display.style-h {
	&.type-b {
		.posts-area {
			align-items: center;
		}
		&.large {
			.posts-thumbnail {
				padding-top: 55%;
				border-radius: 0.25rem;
				overflow: hidden;

				&.type-video::after {
					content: '';
					display: flex;
					justify-content: center;
					align-items: center;
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background-color: rgba(0,0,0,0.2);
					background-repeat: no-repeat;
					background-position: center center;
					background-size: 1.5rem 1.5rem;
					background-image: svgIcon(play, #fff);
				}
			}

			.posts-inform {
				.posts-name {
					@include font-body(medium);

					color: $color-base-090;
				}
			}
		}
	}
}